import React from "react";
import { Box,Button } from '@mui/material';
import { useNavigate } from "react-router-dom";
import emptyCart from "../image/emptyCart.png";

const EmptyCart = () => {
    const navigate = useNavigate();
    const shoppingPage = () => {
        navigate('/');
    }
    return (
        <Box sx={{maxWidth:1000,margin:"auto"}}>
             <Box sx={{textAlign:"center",marginBottom:{xs:24,sm:55,md:10}}}>
                      <Box component="img" sx={{height:250,marginTop:3}} src={emptyCart} alt="image"></Box>
                       <Box>
                              <Button sx={{border:"1px solid #080867",background:"white",color:"#080867",fontWeight:600,boxShadow:"none","&:hover":{background:"white",boxShadow:"none"}}} variant="contained" onClick={shoppingPage}>Continue Shopping</Button>
                       </Box>
              </Box>
        </Box>
    )
}

export default EmptyCart;