import React from 'react';
import { Box, Typography,Button,Grid,Stack } from '@mui/material';
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

const Cart = ({newProduct,setProduct}) => {
  console.log(newProduct.amount);
 
  const navigate = useNavigate();
    function shoppingPage() {
      navigate('/');
    }
    function addressPage() {
        navigate('/PersonalDetails');
    } 

const removeSingleProduct = (name) => {
   const newList = newProduct.filter((product) => product.name !== name);
   setProduct(newList);
}

 var totalPrice = 0 ;
 newProduct.map(cartFinalPrice => {
    totalPrice = totalPrice + cartFinalPrice.amount;
 }) 

console.log(totalPrice);

    return (
       <Box sx={{maxWidth:1000,margin:"auto",paddingLeft:{xs:2,sm:2,md:6},paddingRight:{xs:1,sm:2}}}>
        <Box sx={{marginBottom:20}}>
            <Typography sx={{fontSize:35,paddingBottom:1,borderBottom:"1px solid lightgray"}}>My Shopping Cart</Typography> 
         { 
         newProduct.map(singleItem => {
                    return(  
                        <Grid container spacing={2} sx={{borderBottom:"1px solid lightgray",paddingTop:2,paddingBottom:2}}>
                        <Grid item md={8} sm={8} xs={8}>
                            <Box sx={{display:"flex"}} >
                            <Link to={`/Details/${singleItem.brand}`} style={{textDecoration:"none",color:"black"}}><Box component="img" src={singleItem.image} alt="image" sx={{height:120,minWidth:100,maxWidth:100,border:"1px solid black",padding:1,cursor:"pointer"}}></Box></Link>
                               
                                <Box sx={{paddingLeft:4,paddingTop:1}}>
                                <Link to={`/Details/${singleItem.brand}`} style={{textDecoration:"none",color:"black"}}><Typography sx={{marginBottom:1,"&:hover":{color:"#3B9AE1"}}}>{singleItem.name}</Typography></Link>
                                    <Box sx={{display:"flex"}}>
                                         <Box sx={{color:"gray",paddingRight:2}}>Quantity: {singleItem.quantity}</Box>
                                         <Button variant="text" sx={{padding:0,color:"#080867"}} onClick={()=>removeSingleProduct(singleItem.name)}>Remove</Button>
                                        
                                    </Box> 
                                 </Box> 
                            </Box> 
                        </Grid>
                        <Grid item md={4} sm={4} xs={4}>
                            <Typography sx={{float:"right",fontWeight:700,marginTop:6}}>Rs.{singleItem.amount}</Typography>
                        </Grid>          
                    </Grid>
             )
                 })
             }   
            
                <Box sx={{float:"right"}}>
                    <Typography sx={{float:"right",fontWeight:700,marginTop:2,marginBottom:2}}>Rs.{totalPrice}</Typography>
             
                    <Typography sx={{clear:"both",color:"gray",marginBottom:2}}>Item price includes shipping and handling</Typography>
            
                    <Stack direction="row" sx={{paddingRight:0}} >
                         <Button onClick={shoppingPage} variant="contained" color="warning" sx={{fontSize:10,marginRight:3}}>Continue Shopping</Button>
                        <Button variant="contained" sx={{fontSize:10,background:"#080867","&:hover":{background:"#080867"}}} onClick={addressPage}>Secure Checkout</Button>
                    </Stack>
            </Box>
            </Box>
       </Box>
    )
}
export default Cart;
