import React, { useState } from 'react';
import { BrowserRouter as Router,Route,Routes } from "react-router-dom";
import Header from './components/Header';
import SubNav from './components/SubNav';
import Electronics from './components/Electronics';
import Sports from './components/Sports';
import Outdoor from './components/Outdoor';
import Travel from './components/Travel';
import Health from './components/Health';
import Details from './components/Details';
import Cart from './components/Cart';
import EmptyCart from './components/EmptyCart';
import PersonalDetails from './components/PersonalDetails';
import AddressDetails from './components/AddressDetails';
import Summary from './components/Summary';
// import Footer from './components/Footer';


const App = () => {
 const [product,setProduct] = useState([]);

const cartData =(a) => {
  console.log(a);
 setProduct((prev)=>{return[...prev,a]});
}

    return (
      <div>
       <Header count={product.length}/>
        <SubNav/>
        <Routes>
     <Route path="/" element={ <Electronics/>} /> 
     <Route path="/Sports" element={ <Sports/>} /> 
     <Route path="/Outdoor" element={ <Outdoor/>} />  
     <Route path="/Travel" element={ <Travel/>} />  
     <Route path="/Health" element={ <Health/>} />  
     <Route path="/Details/:brand" element={ <Details cartValue={cartData} />} />  
     <Route path="/Cart" element={ product.length>0?<Cart newProduct={product} setProduct={setProduct} />:<EmptyCart/>} />
     <Route path="/PersonalDetails" element={<PersonalDetails/>} />
     <Route path="/AddressDetails" element={<AddressDetails/>} />
     <Route path="/Summary" element={<Summary productCount={setProduct}/>} />
      </Routes>
      {/* <Footer/>  */}
      </div>
    );
  }

export default App;
