import React,{ useState,useEffect } from 'react';
import Data from './Data.json';
import { Box,Typography,Button,Grid } from '@mui/material';
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag';
import { useParams } from 'react-router-dom';
import CheckCircleRoundedIcon from '@mui/icons-material/CheckCircleRounded';
import AirportShuttleIcon from '@mui/icons-material/AirportShuttle';
import ShowMoreText from "react-show-more-text";
import axios from "axios";

const Details = (props) => {
     const [notify,setNotify] = useState(false);
       const [isReadMore, setIsReadMore] = useState(false);

        const [productCount,setProductCount] = useState(1);
        const [cart,setCart] = useState([]);
        const [warning,setWarning] = useState(false);
     

var finalPrice = 0;
        const [singleProduct,setSingleProduct] = useState([]);
        console.log(singleProduct);

        const { brand } = useParams()

      useEffect(() => {
       console.log("rendering");
            const findProduct = () => {
                 const electronicsProduct = Data.electronics.find((product) => product.brand === brand)
                 const sportsProduct = Data.sports.find((product) => product.brand === brand)
                 const outdoorProduct = Data.outdoor.find((product) => product.brand === brand)
                 const travelProduct = Data.travel.find((product) => product.brand === brand)
                 const healthProduct = Data.health.find((product) => product.brand === brand)

                 setSingleProduct(electronicsProduct || sportsProduct || outdoorProduct || travelProduct || healthProduct)
                      console.log(electronicsProduct,sportsProduct,outdoorProduct,travelProduct,healthProduct)
                 }
       findProduct()
          }, {brand})
        

function handleIncrement() {
    if(productCount < 5){
         setProductCount(productCount+1);
        console.log(productCount);
   }
}

function handleDecrement() {
  if(productCount > 1){
      setProductCount(productCount-1);
      console.log(productCount);
  }
}
   

finalPrice += singleProduct.price * productCount;
console.log(finalPrice);


 const addToCart = (item) => {
 //to post cart page datas            
 const cartData = {
        image:item.image,
        name:item.name,
        quantity:item.quantity,
        amount: item.amount
     };
     axios.post('http://localhost:4000/cartData', cartData, {
       headers: {
         'Content-Type': 'application/json'
       }
     })
     .then(res => {
       console.log(res);
       console.log(res.data);
     });
     
     let isPresent = false;
     cart.forEach((product)=>{
           if(item.id == product.id)
                isPresent = true;
            })
            if(isPresent){
                 setWarning(true);
                 setTimeout(()=>{
                       setWarning(false);
                 },2000);
            return;
            }
            
     let added = true ;
     // cart.forEach((product)=>{
     //      if(item.id != product.id)
     //                        added = true;
     //                    })
     if(added){
          setNotify(true);
          setTimeout(()=>{
               setNotify(false);
          },1000);
     }


      


           setCart([...cart, item]);
            const detail = props.cartValue(item);
            console.log(detail);
}

const toggleBtn = () => {
       setIsReadMore(!isReadMore);
}


    return(
        <Box sx={{maxWidth:1000,margin:"auto",paddingLeft:{xs:2,md:6},paddingRight:{xs:2},paddingTop:4,position:"relative"}}>
              {
                     warning && <Typography sx={{color:"red",textAlign:"center",position:"absolute",right:10,top:100}}>Item is already in cart</Typography>
              }
               <Grid container spacing={2}>
                       <Grid item xs={12} sm={5} md={7} >
                                <Box component="img" sx={{height:390,border:"1px solid grey",width:{xs:290,sm:220,md:480},marginLeft:{xs:3},marginRight:{xs:3},cursor:"pointer"}} src={singleProduct.image}/>
                        </Grid>

                        <Grid item xs={12} sm={7} md={5} >
                                 <Typography sx={{marginBottom:1}}>{singleProduct.name}</Typography>
                                 <Typography color="primary" sx={{textTransform:"uppercase",marginBottom:3}}>{singleProduct.brand}</Typography>
                                 <Box sx={{display:'flex'}}>
                                         <Typography>Price:</Typography>
                                         <Typography sx={{fontWeight:600,marginBottom:2}}>Rs.{singleProduct.price}</Typography>
                                 </Box>
                                 <Box sx={{display:"flex",marginBottom:2}}>
                                         <CheckCircleRoundedIcon sx={{color:"green"}}></CheckCircleRoundedIcon>
                                         <Typography sx={{paddingLeft:1,color:"gray"}}>Item price includes shipping and handling</Typography>
                                  </Box>
                                  <Box sx={{display:"flex"}}>
                                        <Box sx={{display:"flex",marginRight:4}}>
                                               <Box onClick={handleDecrement} sx={{height:"auto",width:10,border:"1px solid grey",padding:1.5,borderRight:"none",background:"lightgrey",cursor:"pointer"}}>-</Box>
                                               <Box sx={{height:"auto",width:10,border:"1px solid grey",padding:1.5}}>{productCount}</Box>
                                               <Box onClick={handleIncrement} sx={{height:"auto",width:10,border:"1px solid grey",padding:1.5,borderLeft:"none",background:"lightgrey",cursor:"pointer"}} >+</Box>
                                        </Box>
                                       <Button variant="contained" sx={{width:270}} startIcon={<ShoppingBagIcon/>} onClick={() => addToCart({value:singleProduct.id,image:singleProduct.image,name:singleProduct.name,quantity:productCount,amount:finalPrice,brand:singleProduct.brand})}>Add to Cart</Button>
                                  </Box>
                                 <Box sx={{display:"flex",borderTop:"1px solid grey",borderBottom:"1px solid grey",marginTop:4,paddingTop:1,paddingBottom:1}}>
                                        <Typography>Total</Typography>
                                        <Typography sx={{marginLeft:"auto"}}>Rs.{finalPrice}</Typography>
                                 </Box>
                                 <Box sx={{display:"flex",marginTop:1}}><AirportShuttleIcon sx={{paddingRight:1}}/><Typography sx={{fontWeight:600,paddingBottom:2}}>Usually ships within 7 business days.</Typography></Box>
                                 <Box sx={{border:"1px solid lightgrey",padding:2}}>
                                   <Typography sx={{fontWeight:700}}>Product description</Typography>
                                   <Typography sx={{paddingTop:1,paddingBottom:1,textTransform:"uppercase",color:'grey',fontWeight:500}}>{singleProduct.name}</Typography>
                                        <ShowMoreText
                                        className='showMoreLess'
                                             lines={3}
                                             onClick={toggleBtn}
                                             expanded={isReadMore}
                                              width={700}
                                          >
                                                  {singleProduct.description}
                                         </ShowMoreText>
                                  </Box>
                      </Grid>
                </Grid>
                {
                    notify ?<Box sx={{backgroundColor:"#D1D1D1",position:"absolute",top:{md:400,xs:"auto"},left:{md:500,xs:100},padding:1}}><Box sx={{display:"flex"}}><CheckCircleRoundedIcon sx={{color:"green",paddingRight:1}}></CheckCircleRoundedIcon> <Typography>Item is added in cart</Typography></Box></Box>:""
}
       </Box>   
    )
}

export default Details;