import React,{useState,useEffect} from 'react';
import { Box,Typography,Button} from '@mui/material';
import tick from '../image/tick.jpg';
import { useNavigate } from "react-router-dom";
import axios from 'axios';

const Summary = ({productCount}) => {
   const navigate = useNavigate();
    const homePage = () => {
        navigate('/')
    } 
    productCount("");


    const [cart,setCart] = useState([]);
    const [personalDetails,setPersonalDetails] = useState([]);
    const [addressDetails,setAddressDetails] = useState([]);
//     useEffect(()=>{
//       getData();
//      }, []);
    
// const getData = () => {
//     axios.get("http://localhost:4000/fetchall/Cart")
//       .then(response => {
//         setData(response.data);
//       })
//       .catch(error => console.log(`error: ${error}`))
// }


    useEffect(()=>{
      axios.get("http://localhost:4000/fetchall/Cart")
      .then(response => {
        setCart(response.data);
      })
        }, []);
        console.log("cdata",cart);

        useEffect(()=>{
            axios.get("http://localhost:4000/fetchall/personalData")
            .then(response => {
              setPersonalDetails(response.data);
            })
              }, []);
              console.log("pdata",personalDetails);

              useEffect(()=>{
                axios.get("http://localhost:4000/fetchall/addressData")
                .then(response => {
                  setAddressDetails(response.data);
                })
                  }, []);
                  console.log("adata",addressDetails);

    return (
      <Box sx={{maxWidth:1000,margin:"auto",textAlign:'center'}}>
          <Box sx={{paddingLeft:{xs:1,md:5},paddingRight:{md:3},paddingTop:2,marginBottom:{xs:22,sm:20,md:12},marginTop:4}}>
          <Box component="img" src={tick} alt="tick" sx={{height:120,width:100}}></Box>
                  <Typography sx={{fontSize:25}}>Order Placed</Typography>
                  <Typography sx={{fontSize:25,marginBottom:6}}>Thank you for your purchase</Typography>
                  <Button variant="contained" sx={{background:"white","&:hover":{background:"white",boxShadow:'none'},color:"#080867",boxShadow:'none',border:"1px solid #080867"}} onClick={homePage}>Continue Shopping</Button>
          <div>
          {
            cart.map(item => (
                <Box key={item.id}>
                    <img src={item.image} alt="product" height="100" width="100"/>
                    <Typography>{item.name}</Typography>
                    <Typography>{item.amount}</Typography>
                    <Typography>{item.quantity}</Typography>
                </Box>
            ))
          }
           {
            personalDetails.map(item => (
                <Box key={item.id}>
                    <Typography>{item.firstName}</Typography>
                    <Typography>{item.lastName}</Typography>
                    <Typography>{item.email}</Typography>
                    <Typography>{item.confirmEmail}</Typography>
                </Box>
            ))
          }
           {
            addressDetails.map(item => (
                <Box key={item.id}>
                    <Typography>{item.addr1}</Typography>
                    <Typography>{item.addr2}</Typography>
                    <Typography>{item.addrType}</Typography>
                    <Typography>{item.city}</Typography>
                    <Typography>{item.phone}</Typography>
                    <Typography>{item.pinCode}</Typography>
                    <Typography>{item.state}</Typography>
                </Box>
            ))
          }
          </div>
                   
          </Box>
     </Box>
    )
}

export default Summary;