import React from 'react';
import SportsData from './Data.json';
import { Box,Typography } from '@mui/material';
import { Link } from "react-router-dom";

const Sports = () => {
    return (
      <Box sx={{maxWidth:1000,margin:"auto",display:"flex",flexWrap:"wrap",flexDirection:"row",overflow:"hidden",paddingLeft:{xs:6,sm:8,md:20},paddingRight:{xs:6}}}>
                 {
                    SportsData.sports.map(item => {
                           return(
                              <Box sx={{height:"auto",width:{xs:250,sm:270,md:270},border:"1px solid grey",marginTop:3,marginBottom:{xs:2},padding:2,marginRight:{sm:3,md:3}}} key={ item.id }>
                              <Link to={`/Details/${item.brand}`} style={{textDecoration:"none",color:"black"}}><Box component="img" sx={{height:180,width:160,marginLeft:{xs:0,sm:5,md:5},marginRight:{xs:0,sm:5,md:5},"&:hover":{transform:"scale(1.1)"},cursor:"pointer"}} src={item.image}/></Link>
                                       <Typography sx={{fontWeight:800,textAlign:"center",textTransform:"uppercase",marginTop:3}}>{item.brand}</Typography>
                                       <Link to={`/Details/${item.brand}`} style={{textDecoration:"none",color:"black"}}>
                                              <Typography sx={{minHeight:90,minWidth:100,cursor:"pointer",marginTop:2,"&:hover":{color:"#3B9AE1"}}}>{item.name}</Typography>
                                        </Link>
                                       <Typography color="primary" sx={{fontWeight:600,textAlign:"center"}}>Rs.{item.price}</Typography>
                                   </Box>
                           )
                       })
                 }
        </Box>
   )
}

export default Sports;